:root {
  --color-main: #A2B08C;
  --color-primary: #DCE3D3;
  --color-secondary: #67613F;
}

.venue-section .ornaments-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.venue-section .ornaments-wrapper.scalex-reverse {
  transform: scaleX(-1);
}
.venue-section .orn-5 {
  position: absolute;
  top: 0;
  left: 0;
  width: 35%;
  transform: translate(-40%, -70%) rotate(-50deg);
}
.venue-section .orn-5 .orn-6 {
  position: absolute;
  top: 0;
  left: 50%;
  width: 80%;
  transform: translate(-95%, 50%) rotate(10deg) scaleY(-1);
}
.venue-section .venue-wrapper .venue-description {
  color: inherit;
}
.venue-section .venue-wrapper .venue-content .frame-venue path {
  fill: var(--color-light);
}
.venue-section .venue-wrapper .venue-content .card {
  color: var(--color-dark);
}
.venue-section .venue-wrapper .venue-content .card::before {
  background-color: var(--color-light);
}